import { useEffect, useState } from "react"
import { Box, Button, TextField, Typography } from "@rp/react/web/mui/core"
import { Warning } from "@rp/react/web/mui/icons"

type Props = {
  initialValue: number
  onChange: (newValue: number) => void
  id: string
  label: string
  min?: number
  max?: number
  step?: number
  warning?: (newValue: number) => boolean
  warningMessage?: string
}

const NumericFeatureInput = ({
  initialValue,
  onChange,
  id,
  label,
  warning,
  warningMessage,
  min = -Infinity,
  max = Infinity,
  step = 1,
}: Props) => {
  const [value, setValue] = useState(initialValue)

  useEffect(() => {
    setValue(initialValue)
  }, [initialValue])

  return (
    <Box sx={{ display: "flex", alignItems: "center", mt: 2, mb: 4, gap: 1 }}>
      <Typography variant="body1" sx={{ whiteSpace: "nowrap" }} component="label" htmlFor={id}>
        {label}
      </Typography>

      <TextField
        size="small"
        value={value}
        id={id}
        onChange={e => {
          const userValue = Number(e.target.value)
          const validValue = Math.min(Math.max(min, userValue), max)
          setValue(validValue)
        }}
        type="number"
        InputProps={{
          endAdornment:
            warning && warning(value) ? (
              <Warning sx={{ ml: 1 }} color="primary" titleAccess={warningMessage} />
            ) : undefined,
        }}
        inputProps={{
          step,
        }}
      />
      <Button
        variant="contained"
        sx={{ height: 40 }}
        onClick={() => onChange(value)}
        disabled={initialValue === value}
      >
        Set
      </Button>
    </Box>
  )
}

export { NumericFeatureInput }
